.mobile-phones-container {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    h1 {
        font-size: 25px;
    }
    &__list {
        display: flex;
        flex-direction: column;
        align-items: center;
        margin-bottom: 40px;
        a {
            color: #000;
            font-weight: bold;
            font-size: 20px;
            line-height: 1.8;
            text-decoration: underline;
        }
    }
}